/*******************************************************************************
 * Styles for the base elements of the theme.
 ******************************************************************************/

h1,
h2,
h3,
h4,
h5,
h6,
em,
div,
li,
span,
strong {
  color: var(--global-text-color);
}

hr {
  border-top: 1px solid var(--global-divider-color);
}

table {
  td,
  th {
    font-size: 1rem;
  }

  th {
    font-weight: bold;
  }
}

a,
table.table a {
  color: var(--global-theme-color);

  &:hover {
    color: var(--global-theme-color);
    text-decoration: underline;
  }

  &:hover:after :not(.nav-item.dropdown) {
    width: 100%;
  }
}

.table-dark {
  background-color: transparent;
}

figure,
img {
  max-width: 90vw;
}

blockquote {
  background: var(--global-bg-color);
  border-left: 2px solid var(--global-theme-color);
  margin: 1.5em 10px;
  padding: 0.5em 10px;
  font-size: 1.2rem;
}

// Math

.equation {
  margin-bottom: 1rem;
  text-align: center;
}

// Caption

.caption {
  font-size: 0.875rem;
  margin-top: 0.75rem;
  margin-bottom: 1.5rem;
  text-align: center;
}

// Card

.card {
  background-color: var(--global-card-bg-color);

  img {
    width: 100%;
  }

  .card-title {
    color: var(--global-text-color);
  }

  .card-item {
    width: auto;
    margin-bottom: 10px;

    .row {
      display: flex;
      align-items: center;
    }
  }
}

// Citation

.citation,
.citation-number {
  color: var(--global-theme-color);
}

// Profile

.profile {
  width: 100%;

  .address {
    margin-bottom: 5px;
    margin-top: 5px;
    font-family: monospace;

    p {
      display: inline-block;
      margin: 0;
    }
  }
}

.profile.float-right {
  margin-left: 1rem;
}

.profile.float-left {
  margin-right: 1rem;
}

@media (min-width: 576px) {
  .profile {
    width: 30%;

    .address {
      p {
        font-size: 0.95rem;
        display: block;
      }
    }
  }
}

.post-description {
  margin-bottom: 2rem;
  font-size: 0.875rem;

  a {
    color: inherit;

    &:hover {
      color: var(--global-theme-color);
      text-decoration: none;
    }
  }
}


// Navbar customization

.navbar {
  box-shadow: none;
  border-bottom: 1px solid var(--global-divider-color);
  background-color: var(--global-bg-color);
  opacity: 0.95;
}

.navbar .dropdown-menu {
  background-color: var(--global-bg-color);
  border: 1px solid var(--global-divider-color);

  a:not(.active) {
    color: var(--global-text-color);
  }

  a:hover {
    color: var(--global-hover-color);
  }

  .dropdown-divider {
    border-top: 1px solid var(--global-divider-color) !important;
  }
}

.dropdown-item {
  color: var(--global-text-color);

  &:hover {
    color: var(--global-hover-color);
    background-color: var(--global-bg-color);
  }
}

.navbar.navbar-light {
  a {
    &:hover {
      text-decoration: none;
    }
  }

  .navbar-brand {
    color: var(--global-text-color);
  }

  .navbar-nav .nav-item .nav-link {
    color: var(--global-text-color);

    &:hover {
      color: var(--global-hover-color);
    }
  }

  .navbar-nav .nav-item.active>.nav-link {
    background-color: inherit;
    font-weight: bolder;
    color: var(--global-theme-color);

    &:hover {
      color: var(--global-hover-color);
    }
  }

  .navbar-brand.social {
    padding-bottom: 0;
    padding-top: 0;
    font-size: 1.7rem;

    a {
      i::before {
        color: var(--global-text-color);
        transition-property: all 0.2s ease-in-out;
      }

      &:hover {
        i::before {
          color: var(--global-theme-color);
        }
      }
    }
  }
}

.navbar-toggler {
  .icon-bar {
    display: block;
    width: 22px;
    height: 2px;
    background-color: var(--global-text-color);
    border-radius: 1px;
    margin-bottom: 4px;
    transition: all 0.2s;
  }

  .top-bar {
    transform: rotate(45deg);
    transform-origin: 10% 10%;
  }

  .middle-bar {
    opacity: 0;
  }

  .bottom-bar {
    transform: rotate(-45deg);
    transform-origin: 10% 90%;
  }
}

.navbar-toggler.collapsed {
  .top-bar {
    transform: rotate(0);
  }

  .middle-bar {
    opacity: 1;
  }

  .bottom-bar {
    transform: rotate(0);
  }
}

#light-toggle {
  padding: 0;
  border: 0;
  background-color: inherit;
  color: var(--global-text-color);

  &:hover {
    color: var(--global-hover-color);
  }
}

// Social (bottom)

.social {
  text-align: center;

  .contact-icons {
    font-size: 4rem;

    a {
      i::before {
        color: var(--global-text-color);
        transition-property: all 0.2s ease-in-out;
      }

      &:hover {
        i::before {
          color: var(--global-theme-color);
        }
      }
    }
  }

  .contact-note {
    font-size: 0.8rem;
  }
}


// Footer
footer.fixed-bottom {
  background-color: var(--global-footer-bg-color);
  font-size: 0.75rem;

  .container {
    color: var(--global-footer-text-color);
    padding-top: 9px;
    padding-bottom: 8px;
  }

  a {
    color: var(--global-footer-link-color);

    &:hover {
      color: var(--global-theme-color);
      text-decoration: none;
    }
  }
}

footer.sticky-bottom {
  border-top: 1px solid var(--global-divider-color);
  padding-top: 40px;
  padding-bottom: 40px;
  font-size: 0.9rem;
}

// CV

.cv {
  margin-bottom: 40px;

  .card {
    background-color: var(--global-card-bg-color);
    border: 1px solid var(--global-divider-color);

    .list-group-item {
      background-color: inherit;
      border-color: var(--global-divider-color);

      .badge {
        color: var(--global-card-bg-color) !important;
        background-color: var(--global-theme-color) !important;
      }
    }
  }
}

.table-cv-map {
  background-color: transparent;
  border: none;
  color: var(--global-text-color);
}

// Awards

.awards-compact {
  display: flex;
  flex-direction: column;
  gap: 3rem;

  .awards-group {
    display: flex;
    flex-direction: column;
    gap: 1.25rem;
  }

  .awards-group__header {
    display: flex;
    align-items: baseline;
    gap: 1rem;

    h2 {
      margin: 0;
      font-size: clamp(1.6rem, 2vw, 1.9rem);
      font-weight: 700;
      color: var(--global-text-color);
    }

    .awards-group__count {
      display: inline-flex;
      align-items: center;
      padding: 0.2rem 0.75rem;
      border-radius: 999px;
      background: rgba(30, 64, 175, 0.08);
      color: var(--global-theme-color);
      font-size: 0.8rem;
      font-weight: 600;
      letter-spacing: 0.02em;
      text-transform: uppercase;
    }
  }

  .awards-stream {
    position: relative;
    list-style: none;
    margin: 0;
    padding: 0;

    &::before {
      content: "";
      position: absolute;
      top: 0.75rem;
      bottom: 0.75rem;
      left: 0.95rem;
      width: 2px;
      background: linear-gradient(180deg, rgba(99, 102, 241, 0.25), rgba(14, 116, 144, 0.25));
    }
  }

  .award-entry {
    position: relative;
    margin: 0;
    padding: 1.1rem 1.25rem 1.1rem 2.65rem;
    background: var(--global-card-bg-color);
    border: 1px solid var(--global-divider-color);
    border-radius: 14px;
    box-shadow: 0 10px 22px rgba(15, 23, 42, 0.08);
    display: grid;
    grid-template-columns: minmax(0, 1fr) auto;
    column-gap: 0.75rem;
    row-gap: 0.45rem;
    transition: border-color 160ms ease, box-shadow 160ms ease, transform 160ms ease;
    --award-marker-rgb: 29, 78, 216;

    &:not(:last-child) {
      margin-bottom: 1.5rem;
    }

    &:hover {
      border-color: rgba(var(--award-marker-rgb), 0.4);
      box-shadow: 0 16px 28px rgba(var(--award-marker-rgb), 0.18);
      transform: translateY(-2px);
    }

    &::before {
      content: "";
      position: absolute;
      top: 1.35rem;
      left: 0.55rem;
      width: 11px;
      height: 11px;
      border-radius: 50%;
      background: rgb(var(--award-marker-rgb));
      box-shadow: 0 0 0 4px var(--global-bg-color);
    }
  }

  .award-entry__title {
    font-size: 1.05rem;
    font-weight: 600;
    color: var(--global-text-color);
    line-height: 1.3;
  }

  .award-entry__badge {
    --badge-bg: rgba(30, 64, 175, 0.12);
    --badge-color: #1d4ed8;
    justify-self: end;
    align-self: start;
    padding: 0.2rem 0.6rem;
    border-radius: 999px;
    background: var(--badge-bg);
    color: var(--badge-color);
    font-size: 0.7rem;
    font-weight: 600;
    letter-spacing: 0.08em;
    text-transform: uppercase;
    white-space: nowrap;
  }

  .award-entry__meta {
    grid-column: 1 / -1;
    display: flex;
    flex-wrap: wrap;
    gap: 0.45rem 0.75rem;
    font-size: 0.88rem;
    color: var(--global-text-color-light);

    .award-entry__org {
      font-weight: 500;
      color: var(--global-text-color);
    }

    a {
      display: inline-flex;
      align-items: center;
      gap: 0.35rem;
      padding: 0.18rem 0.55rem;
      border-radius: 999px;
      border: 1px solid rgba(30, 64, 175, 0.22);
      background: rgba(30, 64, 175, 0.08);
      color: var(--global-theme-color);
      font-size: 0.72rem;
      font-weight: 600;
      letter-spacing: 0.04em;
      text-decoration: none;
      text-transform: uppercase;

      &:hover {
        border-color: rgba(30, 64, 175, 0.4);
        background: rgba(30, 64, 175, 0.14);
      }
    }
  }

  .award-entry__badge[data-badge="best-paper"] {
    --badge-bg: rgba(239, 68, 68, 0.16);
    --badge-color: #b91c1c;
  }

  .award-entry[data-category="best-paper"] {
    --award-marker-rgb: 185, 28, 28;
  }

  .award-entry[data-category="service"] {
    --award-marker-rgb: 67, 56, 202;
  }

  .award-entry__badge[data-badge="research"] {
    --badge-bg: rgba(16, 185, 129, 0.16);
    --badge-color: #047857;
  }

  .award-entry__badge[data-badge="service"] {
    --badge-bg: rgba(79, 70, 229, 0.16);
    --badge-color: #4338ca;
  }

  .award-entry[data-category="research"] {
    --award-marker-rgb: 4, 120, 87;
  }
}

@media (max-width: 640px) {
  .awards-compact {
    .awards-group__header {
      flex-direction: column;
      align-items: flex-start;
      gap: 0.45rem;
    }

    .awards-stream::before {
      left: 0.7rem;
    }

    .award-entry {
      grid-template-columns: minmax(0, 1fr);
      padding: 1rem 1rem 1rem 2.25rem;

      &::before {
        left: 0.45rem;
      }
    }

    .award-entry__badge {
      justify-self: start;
      margin-top: 0.1rem;
    }
  }
}

// Repositories

@media (min-width: 768px) {
  .repo {
    max-width: 50%;
  }
}

// Blog

.header-bar {
  border-bottom: 1px solid var(--global-divider-color);
  text-align: center;
  padding-top: 2rem;
  padding-bottom: 3rem;

  h1 {
    color: var(--global-theme-color);
    font-size: 5rem;
  }
}

.tag-category-list {
  border-bottom: 1px solid var(--global-divider-color);
  text-align: center;
  padding-top: 1rem;

  ul {
    justify-content: center;
    display: flow-root;

    p,
    li {
      list-style: none;
      display: inline-block;
      padding: 1rem 0.5rem;
      color: var(--global-text-color-light);
    }
  }
}

.post-title {
  mjx-container[jax="CHTML"][display="true"] {
    display: unset;
  }
}

.post-list {
  margin: 0;
  margin-bottom: 40px;
  padding: 0;

  li {
    border-bottom: 1px solid var(--global-divider-color);
    list-style: none;
    padding-top: 2rem;
    padding-bottom: 2rem;

    .post-meta {
      color: var(--global-text-color-light);
      font-size: 0.875rem;
      margin-bottom: 0;
    }

    .post-tags {
      color: var(--global-text-color-light);
      font-size: 0.875rem;
      padding-top: 0.25rem;
      padding-bottom: 0;
    }

    a {
      color: var(--global-text-color);
      text-decoration: none;

      &:hover {
        color: var(--global-theme-color);
      }
    }

    mjx-container[jax="CHTML"][display="true"] {
      display: unset;
    }
  }
}

.pagination {
  .page-item {
    .page-link {
      color: var(--global-text-color);

      &:hover {
        color: $black-color;
      }
    }

    &.active .page-link {
      color: $white-color;
      background-color: var(--global-theme-color);

      &:hover {
        background-color: var(--global-theme-color);
      }
    }
  }
}


// Distill

.distill {
  a:hover {
    border-bottom-color: var(--global-theme-color);
    text-decoration: none;
  }
}


// Projects

.projects {
  a {
    text-decoration: none;

    &:hover {
      .card-title {
        color: var(--global-theme-color);
      }
    }
  }

  .card {
    img {
      width: 100%;
    }
  }

  .card-item {
    width: auto;
    margin-bottom: 10px;

    .row {
      display: flex;
      align-items: center;
    }
  }

  .grid-sizer,
  .grid-item {
    width: 240px;
    margin-bottom: 8px;
  }

  .group-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 0.0rem;
    padding: 0.2rem 1rem 0.2rem;
    height: 100%;

    .group-card__avatar {
      width: 170px !important;
      height: 170px !important;
      object-fit: cover;
      margin: 0 auto;
    }

    .group-card__body {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      gap: 0.0rem;
      padding: 0;
      width: 100%;
    }

    .group-card__title {
      font-size: 1.05rem;
      font-weight: 600;
      margin: 0;
      min-height: 1.2rem;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .group-card__subtitle {
      display: block;
      font-size: 0.9rem;
      font-weight: 500;
      color: var(--global-text-color-light);
      min-height: 1.2rem;
    }

    .group-card__next {
      font-size: 0.83rem;
      margin: 0;
      text-align: center;
    }
  }

  h2.category {
    color: var(--global-divider-color);
    border-bottom: 1px solid var(--global-divider-color);
    padding-top: 0.5rem;
    margin-top: 2rem;
    margin-bottom: 1rem;
    text-align: right;
  }
}


// Publications

.publications-archive {
  .pub-note {
    margin: 0 0 1rem;
    font-size: 0.85rem;
    color: var(--global-text-color-light);
  }

  h2.bibliography {
    border-left: none;
    padding-left: 0;
    font-size: 1.6em;
    margin: 2.5em 0 1em;
    font-weight: 600;

    &:first-of-type {
      margin-top: 1.25em;
    }
  }

  ol.bibliography {
    list-style: none;
    margin: 0 0 1.0em;
    padding: 0;
    counter-reset: none;

    > li {
      position: relative;
      padding-left: 1rem;
      margin-bottom: 0.05em;
      line-height: 1.55;
      font-size: 1rem;

      &:last-child {
        margin-bottom: 0;
      }

      &::marker {
        content: none;
      }

      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0.40em;
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background: var(--global-theme-color);
        opacity: 0.9;
      }

      .pub-item {
        display: flex;
        flex-direction: column;
        gap: 0.35rem;
      }

      .pub-line {
        display: grid;
        grid-template-columns: auto minmax(0, 1fr);
        column-gap: 0.45rem;
        row-gap: 0.35rem;
        align-items: start;
      }

      .pub-badge {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        background: rgba(30, 64, 175, 0.1);
        color: var(--global-theme-color);
        font-size: 0.6875rem;
        font-weight: 600;
        line-height: 1;
        padding: 4px 10px 3px;
        border-radius: 999px;
        letter-spacing: 0.25px;
        text-transform: none;
        white-space: nowrap;

        a {
          color: inherit !important;
          text-decoration: none;
        }
      }

      .pub-main {
        display: contents;
      }

      .pub-header {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        gap: 0.55rem;
        grid-column: 2;
      }

      .pub-title {
        font-weight: 600;
        font-size: 1.02rem;
        line-height: 1.25;

        a {
          font-weight: 600;
        }
      }

      .pub-award {
        display: inline-flex;
        align-items: center;
        padding: 2px 8px;
        border-radius: 999px;
        background: rgba(236, 72, 153, 0.9);
        color: #fff;
        font-size: 0.68rem;
        text-transform: uppercase;
        letter-spacing: 0.08em;
      }

      .pub-authors {
        font-size: 0.9rem;
        color: var(--global-text-color-light);
        line-height: 1.00;
        grid-column: 1 / -1;

        a {
          color: inherit;
          text-decoration: none;

          &:hover {
            color: var(--global-theme-color);
            text-decoration: underline;
          }
        }

        strong {
          font-weight: 600;
          color: var(--global-text-color);
        }

        .self-author {
          color: #374151;
        }

        .more-authors {
          color: var(--global-text-color-light);
          text-decoration: none;
          cursor: pointer;

          &:hover {
            color: var(--global-theme-color);
            text-decoration: underline;
          }
        }
      }

      .pub-info {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        gap: 0.35rem;
        font-size: 0.84rem;
        color: var(--global-text-color-light);
        grid-column: 1 / -1;
      }

      .pub-venue {
        color: var(--global-text-color);
        font-weight: 500;
      }

      .pub-links-inline {
        display: inline-flex;
        align-items: center;
        gap: 0.3rem;
        font-size: 0.72rem;
      }

      .pub-link-sep {
        color: var(--global-theme-color);
        opacity: 0.75;
        font-weight: 600;
      }

      .pub-link-sep.light-sep {
        color: var(--global-text-color-light);
      }

      .pub-link {
        display: inline-flex;
        align-items: center;
        padding: 0.12rem 0.45rem;
        border-radius: 999px;
        background: rgba(30, 64, 175, 0.08);
        color: var(--global-theme-color);
        font-size: 0.66rem;
        font-weight: 500;
        line-height: 1;
        text-decoration: none;
        text-transform: uppercase;
        letter-spacing: 0.07em;

        &.abstract,
        &.bibtex {
          cursor: pointer;
        }

        &:hover {
          background: rgba(30, 64, 175, 0.18);
          color: var(--global-theme-color);
        }
      }

      .pub-badges {
        margin-top: 0.0rem;
        grid-column: 1 / -1;
      }

      .hidden {
        display: none;
        font-size: 0.85rem;
        margin-top: 0.0em;

        p {
          line-height: 1.4;
          margin: 0.0rem 1.0;
        }

        pre {
          font-size: 1em;
          line-height: 1.2em;
          padding: 5px;
        }
      }

      .hidden.open {
        display: block;
      }

      // @supports not (display: contents) {
      //   .pub-line {
      //     display: flex;
      //     align-items: flex-start;
      //     gap: 0.45rem;
      //   }

      //   .pub-main {
      //     flex: 1;
      //     display: flex;
      //     flex-direction: column;
      //     gap: 0.3rem;
      //   }

      //   .pub-header {
      //     grid-column: auto;
      //   }

      //   .pub-authors,
      //   .pub-info,
      //   .pub-badges,
      //   .pub-section {
      //     grid-column: auto;
      //   }
      // }
    }
  }
}

@media (min-width: 37.5em) {
  .publications-archive {
    ol.bibliography > li {
      padding-left: 1.0rem;
    }
  }
}

// Rouge Color Customization
figure.highlight {
  margin: 0 0 1rem;
}

pre {
  color: var(--global-theme-color);
  background-color: var(--global-code-bg-color);
  border-radius: 6px;
  padding: 6px 12px;

  pre,
  code {
    background-color: transparent;
    border-radius: 0;
    margin-bottom: 0;
    padding: 0;
  }
}

code {
  color: var(--global-theme-color);
  background-color: var(--global-code-bg-color);
  border-radius: 3px;
  padding: 3px 3px;
}


// Transitioning Themes
html.transition,
html.transition *,
html.transition *:before,
html.transition *:after {
  transition: all 750ms !important;
  transition-delay: 0 !important;
}

// Extra Markdown style (post Customization)
.post {
  .post-meta {
    color: var(--global-text-color-light);
    font-size: 0.875rem;
    margin-bottom: 0;
  }

  .post-tags {
    color: var(--global-text-color-light);
    font-size: 0.875rem;
    padding-top: 0.25rem;
    padding-bottom: 1rem;

    a {
      color: var(--global-text-color-light);
      text-decoration: none;

      &:hover {
        color: var(--global-theme-color);
      }
    }
  }

  .post-content {
    blockquote {
      border-left: 5px solid var(--global-theme-color);
      padding: 8px;

      p {
        margin-bottom: 0;
      }
    }
  }
}

progress {
  /* Positioning */
  position: fixed;
  left: 0;
  top: 56px;
  z-index: 10;

  /* Dimensions */
  width: 100%;
  height: 1px;

  /* Reset the appearance */
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;

  /* Get rid of the default border in Firefox/Opera. */
  border: none;

  /* Progress bar container for Firefox/IE10  */
  background-color: transparent;

  /* Progress bar value for IE10  */
  color: var(--global-theme-color);
}

progress::-webkit-progress-bar {
  background-color: transparent;
}

progress::-webkit-progress-value {
  background-color: var(--global-theme-color);
}

progress::-moz-progress-bar {
  background-color: var(--global-theme-color);
}

.progress-container {
  width: 100%;
  background-color: transparent;
  position: fixed;
  top: 56px;
  left: 0;
  height: 5px;
  display: block;
}

.progress-bar {
  background-color: var(--global-theme-color);
  width: 0%;
  display: block;
  height: inherit;
}

/* Copy button */
.code-display-wrapper {
  position: relative;

  .copy {
    background: var(--global-card-bg-color);
    border-color: var(--global-bg-color);
    border-radius: .3rem;
    border-style: none;
    color: var(--global-text-color);
    font-size: medium;
    opacity: 0;
    position: absolute;
    right: .5rem;
    top: .5rem;
  }

  &:active .copy,
  &:focus .copy,
  &:hover .copy {
    color: var(--global-hover-color);
    opacity: 1;
  }
}

.btn-group.dropdown {
  .btn {
    box-shadow: none;
    -webkit-box-shadow: none;
  }

  .btn-secondary.dropdown-toggle {
    border: 1px solid var(--global-divider-color);

    .page-size {
      color: inherit;
    }

    &:not(.active) {
      background-color: var(--global-bg-color) !important;
      color: var(--global-text-color);
    }

    &:hover {
      background-color: var(--global-hover-color) !important;
      color: var(--global-hover-text-color) !important;
    }
  }

  .dropdown-menu {
    background-color: var(--global-bg-color);
  }

  .dropdown-item {
    background-color: var(--global-bg-color);
    color: var(--global-text-color);

    &:hover {
      color: var(--global-hover-color);
    }
  }

  .dropdown-item.active, .dropdown-item:active {
    background-color: var(--global-hover-color);
    color: var(--global-hover-text-color) !important;

    &:hover {
      color: var(--global-hover-text-color);
    }
  }
}

/* Table of Contents */
nav[data-toggle="toc"] {
  top: 5rem;

  .nav .nav>li>a {
    font-size: .75rem;
  }

  .nav>li>a {
    color: var(--global-text-color);
    font-size: .75rem;

    &:hover {
      color: var(--global-hover-color);
      border-left-color: var(--global-hover-color);
    }
  }

  .nav-link.active {
    color: var(--global-theme-color);
    border-left-color: var(--global-theme-color);
    font-size: .75rem;

    &:hover {
      color: var(--global-hover-color);
      border-left-color: var(--global-hover-color);
    }
  }
}

/* small screens */
@media (max-width: 576px) {
  /* override stickyness so that the navigation does not follow scrolling */
  nav[data-toggle="toc"] {
    visibility: hidden;
    height: 0;
    top: 0;
  }
}

/* Tips, warnings, and dangers blockquotes */
.post .post-content blockquote {
  &.block-tip {
    border-color: var(--global-tip-block);
    background-color: var(--global-tip-block-bg);

    p {
      color: var(--global-tip-block-text);
    }

    h1, h2, h3, h4, h5, h6 {
      color: var(--global-tip-block-title);
    }
  }

  &.block-warning {
    border-color: var(--global-warning-block);
    background-color: var(--global-warning-block-bg);

    p {
      color: var(--global-warning-block-text);
    }

    h1, h2, h3, h4, h5, h6 {
      color: var(--global-warning-block-title);
    }
  }

  &.block-danger {
    border-color: var(--global-danger-block);
    background-color: var(--global-danger-block-bg);

    p {
      color: var(--global-danger-block-text);
    }

    h1, h2, h3, h4, h5, h6 {
      color: var(--global-danger-block-title);
    }
  }
}

.featured-posts {
  a {
    color: var(--global-text-color-light);
    text-decoration: none;

    .card-text {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }

    &:hover {
      color: var(--global-theme-color);

      .card-title {
        color: var(--global-theme-color);
      }
    }
  }

  .card-item {
    margin-bottom: 10px;
  }

  .post-meta {
    color: var(--global-text-color-light);
    font-size: 0.875rem;
    margin-bottom: 0;
    padding-top: 0.5rem;
  }
}
